export const ProjectTile = (props) => {
  return (
    <div className="project">
        <a href={props.link} target="_blank" rel="noreferrer">
            <img src={props.img} alt={props.alt}></img>
        </a>
        <h3>{props.title}</h3>
    </div>
  );
}
